input{
    margin: 10px 5px 10px 0px;
    width: 300px;
    font-size: 13px;
    padding: 6px 0 4px 10px;
    border: 2px solid #980343;
    border-radius: 8px;

}

.formControl.error input {
    border: solid red 2px
}

button {
    margin-top: 20px;
    width: 300px;
    height: 25px;
    border: 3px solid #980343;
    border-radius: 5px;
}

.formControl.error span {
    font-weight: bold;
    color: #d91818;
}
